<template>
  <transition name="opacity">
    <div class="hs-imageview" v-if="currentValue" @click="_close">
      <div class="slide">
        <cube-slide
          ref="slide"
          :data="images"
          :initial-index="0"
          :loop="true"
          :auto-play="false"
          :showDots="false"
          :interval="8000"
          :threshold="0.3"
          :speed="400"
          @change="_changePage">
        </cube-slide>
      </div>
      <div class="tabbar">
        <p>{{title}}</p>
        <p>{{dotsIndex}}<span>/{{images.length}}</span></p>
      </div>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    images: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  watch: {
    currentValue(val) {
      if (val) {
        this.dotsIndex = 1
      }
    }
  },
  data() {
    return {
      dotsIndex: 1
    }
  },
  methods: {
    _close() {
      this.currentValue = false
    },
    _changePage(index) {
      this.dotsIndex = index + 1
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-imageview
  @extend .flex-center
  position absolute
  width 100%
  height 100%
  top 0
  left 0
  background-color black
  z-index 10
  >.slide
    width 100%
    height 230px
  >.tabbar
    @extend .flex-row
    @extend .flex-between
    position absolute
    left 0
    bottom 0
    width 100%
    height 45px
    color $color_white
    font-size 1rem
    padding 0 15px
    span
      font-size 0.8rem
</style>



