<template>
  <div class="infor-details-room">
    <div class="des" @click="extend = !extend">
      <div class="logo" v-lazy:background-image="currentValue.ImageUrl" @click.stop="$emit('delegateWithImage', currentValue.RoomId, currentValue.Name)"></div>
      <div class="middle">
        <div>
          <p>{{currentValue.Name}}</p>
          <div @click.stop="$emit('delegateWithRoom', currentValue)">
            {{$t('base.Details')}}
            <div class="arrow" style="background-image: url(static/image/base/cell/right_arrow_red.png)"></div>
          </div>
        </div>
        <div>{{currentValue.Area}}㎡&nbsp;&nbsp;{{currentValue.Floor}}{{$t('base.Floor')}}</div>
        <div>{{currentValue.Broadnet}}&nbsp;|&nbsp;{{currentValue.BedType}}</div>
        <div class="label">
          <p v-if="Number(currentValue.InfoSource) === 1"
            class="agreement">{{$t('base.Agreement')}}</p>
        </div>
      </div>
      <div class="price">
        <p>￥{{Math.ceil(Math.min(...currentValue.RatePlanList.map(v => v.AverageRate)))}}</p>
        <span>{{$t('base.From')}}</span>
      </div>
      <div class="layer" :style="{display: !extend && showSeparator ? 'block' : 'none'}"></div>
    </div>
    <div class="details" v-show="extend" v-for="(item, index) in currentValue.RatePlanList" :key="index" @click.stop="$emit('delegateWithRoom', currentValue, item)">
      <div class="text">
        <div>
          <p>
            {{item.RatePlanName}}
            <template v-if="item.IsLastMinuteSale">({{$t('base.SpecialOffer')}})</template>
          </p>
          <p v-if="Number(item.InfoSource) === 1"
            class="agreement">{{$t('base.Agreement')}}</p>
        </div>
        <p>{{item.BreakFast}}</p>
        <p>{{currentValue.Area}}㎡&nbsp;&nbsp;{{currentValue.Floor}}{{$t('base.Floor')}}</p>
        <p :class="{warning: item.CancelRuleDesc !== $t('base.CancelForFree')}">{{item.CancelRuleDesc}}</p>
      </div>
      <p class="price">￥{{Math.ceil(item.AverageRate)}}</p>
      <div class="button" :class="{'disable-button': !item.Status}" :type="language" @click.stop="_gotoBookDetails(currentValue, item)">
        <p>{{item.Status ? $t('base.Book') : $t('base.BookFinished')}}</p>
        <p v-if="item.Status">{{item.PaymentType | filterHotelPaymentTypeOptions}}</p>
      </div>
      <div class="layer"></div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
import {mapGetters, mapMutations} from 'vuex'
export default {
  props: {
    value: {
      type: Object,
      default: function() {
        return {}
      }
    },
    showSeparator: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    ...mapGetters([
      'hotelOrder'
    ]),
    currentValue: {
      get: function(val) {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  filters: {
    filterHotelPaymentTypeOptions(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.SelfPay')
        case 2:
          return Vue.prototype.$t('base.Prepay')
        case 3:
          return Vue.prototype.$t('base.Agreement')
        case 4:
          return Vue.prototype.$t('base.InternalProcurement')
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  watch: {
    extend(val) {
      if (val) {
        this.$emit('delegateWithWatch', this.currentValue.RoomId)
      }
    }
  },
  data() {
    return {
      extend: false
    }
  },
  methods: {
    ...mapMutations([
      'SET_HOTEL_ORDER'
    ]),
    _gotoBookDetails(Room, RatePlan) {
      this.$emit('delegateWithBook', Room, RatePlan)
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
@import '~common/stylus/mixin'
.infor-details-room
  width 100%
  &:last-child
    >.des
      >.layer
        display none !important
  >.des
    @extend .flex-row
    @extend .flex-start
    width 100%
    height 120px
    padding 15px
    background-color $color_white
    position relative
    >.logo
      width 90px
      height 90px
      background-size 100% 100%
    >.middle
      width calc(100% - 90px)
      height 100%
      margin-left 10px
      >div
        @extend .flex-row
        @extend .flex-start
        width 100%
        line-height 1.5em
        &:nth-child(1)
          >p
            line-height 1.2em
            ellipsis(2)
            max-height 2.4em
            max-width calc(100% - 50px)
          >div
            @extend .flex-row
            @extend .flex-end
            font-size 0.8rem
            color $color_theme
            margin-left 5px
            flex-shrink 0
            >.arrow
              width 17px
              height 17px
              background-size 100% 100%
        &:nth-child(2)
        &:nth-child(3)
          font-size 0.8rem
          color $color_gray_text
      >.label
        @extend .flex-row
        @extend .flex-start
        line-height 1em
        margin-top 5px
        >.category
        >.agreement
          font-size 0.7rem
          margin-right 5px
          padding 2px
          border 1px solid $color_green_text
          color $color_green_text
          border-radius 2px
        >.agreement
          border 1px solid $color_blue_text
          color $color_blue_text
    >.price
      @extend .flex-row
      @extend .flex-end
      width 80px
      position absolute
      top 50%
      right 15px
      transform translate(0, -50%)
      >p
        font-size 1rem
        color $color_theme
        font-weight bold
      >span
        font-size 0.8rem
    >.layer
      position absolute
      height 1px
      bottom 0px
      left 15px
      right 15px
      border-bottom 1px solid $color_gray_layer
  >.details
    @extend .flex-row
    @extend .flex-between
    width 100%
    padding 15px
    background-color $color_gray_background_0
    position relative
    >.text
      width calc(100% - 80px - 45px - 20px)
      >div
        @extend .flex-row
        @extend .flex-start
        >p
          line-height 1.2em
        >.agreement
          font-size 0.7rem
          margin-left 5px
          padding 2px
          border 1px solid $color_blue_text
          color $color_blue_text
          border-radius 2px
      >p
        line-height 1.5em
        &:nth-child(2)
          font-size 0.8rem
        &:nth-child(3)
          font-size 0.8rem
          color $color_gray_text
        &:nth-child(4)
          font-size 0.8rem
          color $color_green_text
      >.warning
        color $color_theme !important
    >.price
      width 75px
      text-align right
      color $color_theme
      font-weight bold
    >.button
      width 50px
      border-radius 4px
      border 1px solid $color_theme
      >p
        width 100%
        padding 5px 2px
        text-align center
        &:nth-child(1)
          background-color $color_theme
          color $color_white
        &:nth-child(2)
          background-color $color_white
          color $color_theme
          font-size 0.8rem
    >.button[type="EN"]
      >p
        font-size 0.6rem
    >.disable-button
      border 1px solid $color_gray_text
      >p
        &:nth-child(1)
          background-color $color_gray_text
    >.layer
      position absolute
      height 1px
      bottom 0
      left 15px
      right 15px
      border-bottom 1px solid $color_gray_layer
    &:last-child
      >.layer
        display none
</style>



