<template>
  <hs-dialog ref="dialog" v-model="currentValue">
    <hs-scroll class="dialog" :data="[details]" @click.native.stop>
      <ul>
        <div class="des-header">
          <p>{{details.Name}}</p>
          <div @click="_close" style="background-image: url('/static/image/header/close-white.png')"></div>
        </div>
        <div class="room-slide">
          <cube-slide
            v-if="showSlide"
            ref="room-slide"
            :initial-index="0"
            :loop="true"
            :auto-play="false"
            :showDots="false"
            :interval="8000"
            :threshold="0.3"
            :speed="400"
            @change="_changePage">
            <div class="room-slide-image" v-lazy:background-image="details.ImageUrl"></div>
          </cube-slide>
        </div>
        <div class="details">
          <div class="item">
            <div>{{$t('base.BedType')}}</div>
            <div>{{details.BedType}}</div>
          </div>
          <div class="item">
            <div>{{$t('base.Area')}}</div>
            <div>{{details.Area}}㎡</div>
          </div>
          <div class="item">
            <div>{{$t('base.Floor')}}</div>
            <div>{{details.Floor}}{{$t('base.Floor')}}</div>
          </div>
          <div class="item">
            <div>{{$t('base.Number')}}</div>
            <div>{{$t('base.PeopleStaying', 'function', details.Capcity)}}</div>
          </div>
          <div class="item">
            <div>{{$t('base.Network')}}</div>
            <div>{{details.Broadnet}}</div>
          </div>
          <div class="cancel" v-if="!noValue(JSON.stringify(itemDetails))">
            <div>{{$t('base.CancelRules')}}: {{itemDetails.CancelRuleDesc}}</div>
            <!-- <div>{{itemDetails.CancelRuleDesc}}</div> -->
          </div>
        </div>
        <div class="footer-button" v-if="noValue(JSON.stringify(itemDetails))">
          <hs-button width="80%" height="40px" @click="_delegateWithWatch">{{$t('base.SeeAll', 'function', details.RatePlanList ? details.RatePlanList.length : 0)}}</hs-button>
        </div>
        <div class="footer" v-else>
          <div>
            <span>{{$t('base.NightsTotalPrice', 'function', getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate))}}</span>
            <p>￥{{Math.ceil(itemDetails.TotalRate)}}</p>
          </div>
          <div :class="{'disable-button': !itemDetails.Status}" @click="_delegateWithBook">{{$t('base.Book')}}</div>
        </div>
      </ul>
    </hs-scroll>
  </hs-dialog>
</template>
<script type="text/ecmascript-6">
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsButton from 'base/hs-button/hs-button'
import HsScroll from 'base/hs-scroll/hs-scroll'
import {mapGetters} from 'vuex'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    },
    itemDetails: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  computed: {
    ...mapGetters([
      'hotelOrder'
    ]),
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  watch: {
    currentValue(val) {
      if (val) {
        setTimeout(() => {
          this.showSlide = true
        }, 500)
      } else {
        this.showSlide = false
      }
    }
  },
  data() {
    return {
      showSlide: false
    }
  },
  methods: {
    _changePage(index) {
    },
    _delegateWithWatch() {
      this._close()
      this.$emit('delegateWithWatch', this.details.RoomId)
    },
    _delegateWithBook() {
      this._close()
      this.$emit('delegateWithBook', this.details, this.itemDetails)
    },
    _close() {
      this.$refs.dialog.showDetails = false
    }
  },
  components: {
    HsDialog,
    HsButton,
    HsScroll
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.des-header
  @extend .flex-row
  @extend .flex-between
  position absolute
  width 100%
  height 44px
  top 0
  left 0
  // background: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  padding 0 15px
  z-index 2
  >p
    font-size 1rem
    font-weight bold
    color $color_white
    text-shadow 0 0 8px $color-black_1
  >div
    width 20px
    height 20px
    background-size 100% 100%
    border-radius 10px
    background-color $color-black_3
.dialog
  // @extend .flex-column
  // @extend .flex-end
  position absolute !important
  width 100%
  left 0
  bottom 0
  background-color $color_white
  max-height 90%
  overflow hidden
  .room-slide
    width 100%
    height 230px
    position relative
    transform: translateZ(0px)
    .room-slide-image
      width 100%
      height 100%
      background-size 100% auto
      background-position center
      background-repeat no-repeat
      transition all 400ms
    .room-slide-image[lazy=error]
      background-image url('../../../../static/image/base/hotel_error.jpeg') !important
  .details
    width 100%
    padding 10px 20px
    >.item
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 5px 0
      >div
        &:nth-child(1)
          width 80px
          font-size 0.9rem
          color $color_gray_text
        &:nth-child(2)
          width calc(100% - 80px)
          font-size 0.9rem
          color $color_black_text_1
    >.cancel
      @extend .flex-column
      @extend .flex-start
      @extend .flex-top
      width 100%
      color $color_theme !important
      font-size 0.9rem
      >div
        margin-top 5px
        line-height 1.2em
  .footer
    @extend .flex-row
    @extend .flex-center
    height 50px
    width 100%
    >div
      height 100%
      &:nth-child(1)
        @extend .flex-column
        @extend .flex-center
        @extend .flex-top
        padding 5px 20px
        color $color_theme
        font-size 1.2rem
        width calc(100% - 120px)
        border-top 1px solid $color_gray_layer
        >span
          font-size 0.7rem
          color $color_black_text_0
          font-weight bold
          margin-bottom 3px
        >p
          font-weight bold
          margin-left -5px
      &:nth-child(2)
        @extend .flex-center
        width 120px
        background-color $color_theme
        color $color_white
        font-size 1rem
    >.disable-button
      pointer-events none
      background-color $color_gray_text !important
  .footer-button
    @extend .flex-center
    height 60px
    width 100%
</style>



