<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="currentValue">
      <c-header isBackNav :title="$t('base.HotelDetails')"></c-header>
      <hs-scroll
        class="table-view"
        :data="[details]">
        <ul>
          <hs-section v-if="!noValue(JSON.stringify(details.Facilities))">
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-logos" :type="language">
                <div class="item" v-if="details.Facilities.indexOf(1) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/FREE_WIFI.png')"></div>
                  <p>{{$t('base.FreeWiFi')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(2) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Charge_WIFI.png')"></div>
                  <p>{{$t('base.PaidWiFi')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(3) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/FREE_broadband.png')"></div>
                  <p>{{$t('base.FreeBoardband')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(4) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Charge_broadband.png')"></div>
                  <p>{{$t('base.PaidBoardband')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(5) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/FREE_PARK.png')"></div>
                  <p>{{$t('base.FreeParking')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(6) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Charge_PARK.png')"></div>
                  <p>{{$t('base.PaidParking')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(7) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/FREE_jieji.png')"></div>
                  <p>{{$t('base.FreePickUpService')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(8) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Charge_jieji.png')"></div>
                  <p>{{$t('base.PaidPickUpService')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(9) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Swimming.png')"></div>
                  <p>{{$t('base.SwimmingPool')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(11) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Gym.png')"></div>
                  <p>{{$t('base.Gym')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(12) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Business_Centre.png')"></div>
                  <p>{{$t('base.BusinessCenter')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(13) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Meeting.png')"></div>
                  <p>{{$t('base.MeetingRoom')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(14) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Restaurant.png')"></div>
                  <p>{{$t('base.HotelRestaurant')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(15) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Clock.png')"></div>
                  <p>{{$t('base.MorningCall')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(16) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Luggage.png')"></div>
                  <p>{{$t('base.LuggageStorage')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(17) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Double_bed.png')"></div>
                  <p>{{$t('base.TwinBed')}}</p>
                </div>
                <div class="item" v-if="details.Facilities.indexOf(18) >= 0">
                  <div class="logo" style="background-image: url('static/image/hotel/service/Big_bed.png')"></div>
                  <p>{{$t('base.BigBed')}}</p>
                </div>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell">
                <p class="title">{{$t('base.GeneralAmenities')}}</p>
                <p class="small">{{details.GeneralAmenities}}</p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell">
                <p class="title">{{$t('base.HotelIntroduction')}}</p>
                <p class="small" v-html="details.Description"></p>
                <p class="title">{{$t('base.ContactHotel')}}</p>
                <p class="normal">{{$t('base.ContactNumber')}}: <span>{{details.Phone}}</span></p>
                <p class="title">{{$t('base.HotelRating')}}</p>
                <p class="normal">{{details.StarRate | filterStarRateOptions}}</p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell">
                <template v-if="!noValue(details.Traffic)">
                  <p class="title">{{$t('base.Traffic')}}</p>
                  <p class="normal">{{details.Traffic}}</p>
                </template>
                <template v-if="!noValue(details.Features)">
                  <p class="title">{{$t('base.FeaturedIntroduction')}}</p>
                  <p class="normal">{{details.Features}}</p>
                </template>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  filters: {
    filterStarRateOptions(val) {
      switch (val) {
        case 0:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 1:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 2:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 3:
          return `${Vue.prototype.$t('base.StarComfort')}${Vue.prototype.$t('base.ClassHotel')}`
        case 4:
          return `${Vue.prototype.$t('base.StarPremium')}${Vue.prototype.$t('base.ClassHotel')}`
        case 5:
          return `${Vue.prototype.$t('base.StarLuxury')}${Vue.prototype.$t('base.ClassHotel')}`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
@import '~common/stylus/mixin'
.hs-container-child
  position absolute
  width 100%
  height 100%
  top 0
  left 0
  background-color $color_white
  z-index 10
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .custom-cell-logos
      @extend .flex-row
      @extend .flex-start
      flex-wrap wrap
      width 100%
      padding 10px 0
      >.item
        @extend .flex-column
        @extend .flex-center
        width 25%
        height 70px
        >.logo
          width 30px
          height 30px
          background-size 100% 100%
        >p
          font-size 0.9rem
          color $color_gray_text
          margin-top 8px
          ellipsis(1)
          height 1.1em
    .custom-cell-logos[type="EN"]
      >.item
        width 50%
        >p
          font-size 0.7rem
    .custom-cell
      width 100%
      padding 15px 0
      >.title
        @extend .flex-row
        @extend .flex-start
        width 100%
        font-size 1.1rem
        font-weight bold
        height 36px
      >.small
        @extend .flex-row
        @extend .flex-start
        width 100%
        font-size 0.7rem
        line-height 1.8em
        color $color_black_text_0
        text-align justify
      >.normal
        @extend .flex-row
        @extend .flex-start
        width 100%
        font-size 0.9rem
        line-height 1.8em
        color $color_black_text_0
        >span
          color $color_theme
</style>



