<template>
  <div class="hs-container" v-if="!noValue(JSON.stringify(details))">
    <c-header class="c-header" isBackNav backgroundColor="none" :style="{opacity: headerOpacity1}"></c-header>
    <c-header class="c-header" isBackNav :title="details.HotelName" :style="{opacity: headerOpacity2}"></c-header>
    <hs-scroll
      ref="tableView"
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getDetails"
      :isListenScroll="true"
      @scroll="_scroll">
      <ul>
        <div class="slide">
          <cube-slide
            ref="slide"
            :initial-index="0"
            :loop="true"
            :auto-play="true"
            :interval="8000"
            :threshold="0.3"
            :speed="400">
            <div class="item" v-for="(item, index) in imageList.filter(v => v.image)" :key="index">
              <img :src="item.image"/>
            </div>
            <div class="item" v-if="!imageList || noValue(JSON.stringify(imageList.filter(v => v.image)))">
              <img src="static/image/base/hotel_error.jpeg"/>
            </div>
          </cube-slide>
        </div>
        <hs-section>
          <hs-cell-1 @click.native="showHotelDetails = true">
            <div class="custom-cell-title">
              <div class="right">
                <p class="name">{{details.HotelName}}</p>
                <div class="logos">
                  <div class="logo" v-if="details.Facilities.indexOf(1) >= 0" style="background-image: url('static/image/hotel/service/FREE_WIFI.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(2) >= 0" style="background-image: url('static/image/hotel/service/Charge_WIFI.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(3) >= 0" style="background-image: url('static/image/hotel/service/FREE_broadband.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(4) >= 0" style="background-image: url('static/image/hotel/service/Charge_broadband.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(5) >= 0" style="background-image: url('static/image/hotel/service/FREE_PARK.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(6) >= 0" style="background-image: url('static/image/hotel/service/Charge_PARK.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(7) >= 0" style="background-image: url('static/image/hotel/service/FREE_jieji.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(8) >= 0" style="background-image: url('static/image/hotel/service/Charge_jieji.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(9) >= 0" style="background-image: url('static/image/hotel/service/Swimming.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(11) >= 0" style="background-image: url('static/image/hotel/service/Gym.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(12) >= 0" style="background-image: url('static/image/hotel/service/Business_Centre.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(13) >= 0" style="background-image: url('static/image/hotel/service/Meeting.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(14) >= 0" style="background-image: url('static/image/hotel/service/Restaurant.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(15) >= 0" style="background-image: url('static/image/hotel/service/Clock.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(16) >= 0" style="background-image: url('static/image/hotel/service/Luggage.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(17) >= 0" style="background-image: url('static/image/hotel/service/Double_bed.png')"></div>
                  <div class="logo" v-if="details.Facilities.indexOf(18) >= 0" style="background-image: url('static/image/hotel/service/Big_bed.png')"></div>
                </div>
                <div class="label">
                  <p class="category">{{details.Category}}</p>
                  <p v-if="Number(details.InfoSource) === 1"
                    class="agreement">{{$t('base.Agreement')}}</p>
                </div>
              </div>
              <div class="left" v-if="language === 'CN'">
                <p>{{$t('base.HotelDetails')}}</p>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 @click.native="showMap = true" logoName="address.png">
            <div class="custom-cell-title">
              <div class="right">
                <div class="address">
                  <div class="logo" style="background-image: url('static/image/hotel/location.png')"></div>
                  <p>{{hotelOrder.CityName}}{{details.DistrictName}}{{details.Address}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 logoName="call.png" @click.native="_callPhone(details.Phone)">
            <div class="custom-cell-title">
              <div class="right">
                <p>{{$t('base.ContactHotel')}}: {{details.Phone}}</p>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false" @click.native="showDatePicker = true">
            <div class="custom-cell-date">
              <div class="date">
                <p>{{$t('base.CheckInDate')}}</p>
                <p>{{$t(beTime(new Date(hotelOrder.CheckInDate)), 'date', ['MM月dd日', 'MM dd'])}}<span>{{getWeeks(hotelOrder.CheckInDate)}}</span></p>
              </div>
              <div class="middle">
                <div class="layer"></div>
                <div class="number">{{$t('base.TotalNights', 'function', getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate))}}</div>
                <div class="layer"></div>
              </div>
              <div class="date">
                <p>{{$t('base.CheckOutDate')}}</p>
                <p>{{$t(beTime(new Date(hotelOrder.CheckOutDate)), 'date', ['MM月dd日', 'MM dd'])}}<span>{{getWeeks(hotelOrder.CheckOutDate)}}</span></p>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="fast-filter-bar">
              <div :class="{'active': fastFilterBreakfast}" @click="fastFilterBreakfast = !fastFilterBreakfast">{{$t('base.Breakfast')}}</div>
            </div>
          </hs-cell-1>
          <infor-details-room
            :id="`infor-details-room-${item.RoomId}`"
            :ref="`infor-details-room-${item.RoomId}`"
            v-for="(item, index) in details.RoomList" :key="index"
            v-model="details.RoomList[index]"
            :showSeparator="index + 1 < details.RoomList.length"
            @delegateWithWatch="_delegateWithWatch"
            @delegateWithImage="_delegateWithImage"
            @delegateWithRoom="_delegateWithRoom"
            @delegateWithBook="_delegateWithBook"></infor-details-room>
          <hs-cell-1 :showLogo="false" v-show="details.RoomList.length === 0">
            <div class="fast-filter-warning">
              {{$t('base.SorryNoMatchingRoom')}}
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>
    <infor-details-map v-model="showMap" :details="details"></infor-details-map>
    <hs-imageview v-model="showImageView" :title="imageViewTitle" :images="imageViewDetails"></hs-imageview>
    <infor-details-room-des
      v-model="showRoomDes"
      :details="roomDesDetails"
      :itemDetails="roomDesItemDetails"
      @delegateWithWatch="_delegateWithWatch"
      @delegateWithBook="_delegateWithBook"></infor-details-room-des>
    <infor-details-hotel v-model="showHotelDetails" :details="details"></infor-details-hotel>
    <hs-date-picker-2
      :show.sync="showDatePicker"
      :fromDate="hotelOrder.CheckInDate"
      @update:fromDate="_updateFromDate"
      :toDate="hotelOrder.CheckOutDate"
      @update:toDate="_updateToDate"></hs-date-picker-2>
  </div>
</template>
<script type="text/ecmascript-6">
// 预订酒店信息详情页面
import Vue from 'vue'
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import InforDetailsRoom from './infor-details-room'
import InforDetailsRoomDes from './infor-details-room-des'
import InforDetailsMap from './infor-details-map'
import InforDetailsHotel from './infor-details-hotel'
import HsImageview from 'base/hs-imageview/hs-imageview'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsDatePicker2 from 'base/hs-date-picker/hs-date-picker-2'
import {kpiHotelGetHotelDetail} from 'api'
import {mapGetters, mapMutations} from 'vuex'
export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (vm.noValue(vm.hotelOrder.TravelType)) {
        vm.$router.replace('/home')
        return
      }
      if (from.path === '/hotel/inforList' || vm.noValue(JSON.stringify(vm.details))) {
        vm.fastFilterBreakfast = false
        vm.details = {}
        vm._getDetails()
      }
      setTimeout(() => {
        vm.$refs.tableView && vm.$refs.tableView.scrollTo(0, 0, 300)
        vm._scroll({y: 0})
      }, 400)
    })
  },
  beforeRouteLeave(to, from, next) {
    if (this.showImageView || this.showMap || this.showHotelDetails || this.showDatePicker) {
      this.showImageView = false
      this.showMap = false
      this.showHotelDetails = false
      this.showDatePicker = false
      next(false)
    } else {
      this.showRoomDes = false
      next()
    }
  },
  computed: {
    ...mapGetters([
      'hotelOrder'
    ]),
    imageList() {
      let res = []
      // eslint-disable-next-line
      this.details.ImageList && this.details.ImageList.forEach(element => {
        if (element.TypeDesc === '外观' || element.TypeDesc === '公共区域') {
          element.Images.forEach(elementChild => {
            res.push({image: elementChild})
          })
        }
      })
      return res
    }
  },
  filters: {
    filterStarRateOptions(val) {
      switch (val) {
        case 0:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 1:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 2:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 3:
          return `${Vue.prototype.$t('base.StarComfort')}${Vue.prototype.$t('base.ClassHotel')}`
        case 4:
          return `${Vue.prototype.$t('base.StarPremium')}${Vue.prototype.$t('base.ClassHotel')}`
        case 5:
          return `${Vue.prototype.$t('base.StarLuxury')}${Vue.prototype.$t('base.ClassHotel')}`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  watch: {
    fastFilterBreakfast() {
      this._getDetails()
    }
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {},
      showImageView: false,
      showMap: false,
      showRoomDes: false,
      showHotelDetails: false,
      showDatePicker: false,
      roomDesDetails: {},
      roomDesItemDetails: {},
      imageViewDetails: [],
      imageViewTitle: '',
      headerOpacity1: 1,
      headerOpacity2: 0,
      fastFilterBreakfast: false
    }
  },
  methods: {
    ...mapMutations([
      'SET_HOTEL_ORDER'
    ]),
    _getDetails() {
      let params = {
        CheckInDate: this.beTimeNumber(this.hotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.hotelOrder.CheckOutDate),
        HotelID: this.$route.query.HotelID
      }
      kpiHotelGetHotelDetail(params).then((res) => {
        console.log('酒店详情: ', res)
        this.details = res.ResultData
        // 只显示含早
        if (this.fastFilterBreakfast) {
          this.details.RoomList.forEach((element, index) => {
            let list = []
            element.RatePlanList.forEach((elementChild, indexChild) => {
              elementChild.BreakFast !== this.$t('base.NoBreakfast') && list.push(elementChild)
            })
            this.details.RoomList[index].RatePlanList = list
          })
          let list = []
          this.details.RoomList.forEach((element, index) => {
            element.RatePlanList.length > 0 && list.push(this.details.RoomList[index])
          })
          this.details.RoomList = list
        }
      })
    },
    _delegateWithImage(RoomId, title) {
      let res = []
      this.details.ImageList && this.details.ImageList.forEach(element => {
        if (element.RoomId === RoomId) {
          element.Images.forEach(elementChild => {
            res.push({image: elementChild})
          })
        }
      })
      this.imageViewDetails = res
      this.imageViewTitle = title
      this.showImageView = true
    },
    _delegateWithRoom(details, item) {
      this.showRoomDes = true
      this.roomDesDetails = details
      this.roomDesItemDetails = item
    },
    _scroll(pos) {
      if (pos.y >= 0) {
        this.headerOpacity1 = 1
        this.headerOpacity2 = 0
      } else if (pos.y < 0 && pos.y > -185) {
        this.headerOpacity1 = 1 - Math.abs(pos.y) / 185
        this.headerOpacity2 = 0
      } else {
        this.headerOpacity1 = 0
        this.headerOpacity2 = 1
      }
    },
    // 打电话
    _callPhone(mobile) {
      window.location.href = `tel:${mobile}`
    },
    // 展开房型cell
    _delegateWithWatch(RoomId) {
      this.$refs[`infor-details-room-${RoomId}`][0].extend = true
      setTimeout(() => {
        this.$refs.tableView.scrollToElement(`#infor-details-room-${RoomId}`, 300, 0, -45)
      }, 100)
    },
    // 选择入住日期回调
    _updateFromDate(val) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.CheckInDate = val
      this.SET_HOTEL_ORDER(res)
    },
    // 选择离店日期回调
    _updateToDate(val) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.CheckOutDate = val
      this.SET_HOTEL_ORDER(res)
      !this.noValue(val) && this._getDetails()
    },
    // 预定
    _delegateWithBook(Room, RatePlan) {
      if (!RatePlan.Status) {
        return
      }
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Hotel = this.details
      res.HotelID = this.details.HotelId
      res.RoomID = Room.RoomId
      res.RatePlanID = RatePlan.RatePlanId
      res.OrderType = RatePlan.PaymentType
      this.SET_HOTEL_ORDER(res)
      this.$router.push({name: 'hotelBookDetails', params: {hotel: this.details, room: Room, ratePlan: RatePlan}})
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1,
    InforDetailsRoom,
    InforDetailsRoomDes,
    InforDetailsMap,
    InforDetailsHotel,
    HsImageview,
    HsDialog,
    HsDatePicker2
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.c-header
    position absolute
    width 100%
    top 0
    left 0
    z-index 9
  >.table-view
    width 100%
    height 100%
    overflow hidden
    .slide
      width 100%
      height 230px
      transform: translateZ(0px)
      overflow: hidden
      position relative
      .item
        @extend .flex-column
        @extend .flex-end
        width 100vw
        height 100%
        float left
        text-align center
        overflow hidden
        >img
          height 100%
          min-width 100%
    .custom-cell-title
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 15px 0
      >.right
        width calc(100% - 60px)
        >p
          font-size 0.9rem
          color $color_black_text_0
          line-height 1.5em
        >.name
          font-size 1.2rem
          font-weight bold
          line-height 1.2em
        >.logos
          @extend .flex-row
          @extend .flex-start
          flex-wrap wrap
          width 100%
          margin 3px 0
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            margin 5px 10px 5px 0
        >.address
          @extend .flex-row
          @extend .flex-start
          @extend .flex-top
          width 100%
          >.logo
            width 16px
            height 16px
            background-size 100% 100%
            margin-right 4px
            margin-top 2px
          >p
            width calc(100% - 20px)
            font-size 0.8rem
            color $color_black_text_0
            line-height 1.5em
        >.label
          @extend .flex-row
          @extend .flex-start
          >.category
          >.agreement
            font-size 0.7rem
            margin-right 5px
            padding 2px
            border 1px solid $color_green_text
            color $color_green_text
            border-radius 2px
          >.agreement
            border 1px solid $color_blue_text
            color $color_blue_text
      >.left
        width 60px
        font-size 0.8rem
        color $color_theme
        text-align right
    .custom-cell-date
      @extend .flex-row
      @extend .flex-around
      width 100%
      padding 15px 0
      >.date
        @extend .flex-column
        @extend .flex-between
        height 36px
        >p
          &:nth-child(1)
            font-size 0.7rem
            color $color_gray_text
          &:nth-child(2)
            font-size 1rem
            color $color_black_text_0
            >span
              font-size 0.7rem
              margin-left 3px
      >.middle
        @extend .flex-row
        @extend .flex-center
        margin 0 10px
        height 100%
        >.number
          @extend .flex-center
          font-size 0.7rem
          color $color_gray_text
          padding 0 8px
          height 20px
          border-radius 10px
          border 1px solid $color_gray_text
          background-color $color_white
        >.layer
          width 10px
          height 1px
          background-color $color_gray_text
    .fast-filter-bar
      @extend .flex-row
      @extend .flex-start
      width 100%
      >div
        font-size 0.8rem
        color $color_black_text_1
        border-radius 2px
        border 1px solid $color_gray_layer
        padding 5px 10px
      >.active
        color $color_theme
        border 1px solid $color_theme
    .fast-filter-warning
      @extend .flex-row
      @extend .flex-start
      font-size 0.8rem
      color $color_theme
      line-height 1.5em
</style>



